<template>
  <div>
    <v-container>
      <v-layout row wrap v-if="currentUser.role === 'administrator'">
        <v-flex xs12 md4 offset-md8 class="pr-1" right>
          <v-select
            v-model="auditorId"
            :items="auditors"
            item-text="fullname"
            item-value="id"
            box
            label="Auditors"
          ></v-select>
        </v-flex>
      </v-layout>
      <v-layout row wrap>
        <v-flex xs12 md12 class="pr-1">
          <v-card class="mb-2">
            <v-card-title
              class="headline font-weight-regular blue-grey white--text"
            >Audit Assignments</v-card-title>
            <v-card-text>
              <v-data-table :headers="tableHeaders" :items="audits" class="evelation-1">
                <template slot="items" slot-scope="{ item }">
                  <tr
                    @click="auditPhysician(item.npi, item.provider_audit_id)"
                    style="cursor:pointer;"
                  >
                    <td>{{ getStatus(item) }}</td>
                    <td>{{ item.provider_name }}</td>
                    <td>{{ item.entity_name }} - {{ item.group_practice }}</td>
                    <td>{{ item.audit_date }}</td>
                    <td>{{ item.med_system }}</td>
                    <td>{{ item.billing_system }}</td>
                    <td>{{ item.audit_type }}</td>
                    <td>{{ calcIncomplete(item) }}</td>
                    <td>{{ item.complete }}</td>
                  </tr>
                </template>
              </v-data-table>
            </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import Config from "../config";
import Router from "../router";
import httpService from "../httpService";

export default {
  name: "Main",
  components: {},
  data: () => ({
    audits: [],
    auditorId: null,
    tableHeaders: [
      { text: "Status", value: "status" },
      { text: "Provider", value: "provider_name" },
      { text: "Practice", value: "practice" },
      { text: "Audit Date", value: "audit_date" },
      { text: "Med System", value: "med_system" },
      { text: "Billing System", value: "billing_system" },
      { text: "Audit Type", value: "audit_type" },
      { text: "In Progress", value: "in_progress" },
      { text: "Completed", value: "complete" }
    ]
  }),
  async mounted() {
    this.auditorId = this.currentUser.id; // when are computed items ready?
    this.getAudits();
    // httpService.get(
    //   `${Config.apiRoot}/audit/dashboard/${this.auditorId}`,
    //   (status, data) => {
    //     this.audits = data.message;
    //   }
    // );
  },
  watch: {
    auditorId: function(id) {
      this.getAudits();
    }
  },
  computed: {
    auditors() {
      return this.$store.getters.ALL_USERS;
    },
    currentUser() {
      return this.$store.getters.CURRENT_USER;
    },
    physicians() {
      return this.$store.getters.PHYSICIAN;
    },
    physicianLookup() {
      return this.physicians.map(row => {
        let res = {};
        res.name = row.last_name + ", " + row.first_name;
        res.npi = row.npi;
        return res;
      });
    }
  },
  methods: {
    getAudits() {
      httpService.get(
        `${Config.apiRoot}/audit/dashboard/${this.auditorId}`,
        (status, data) => {
          this.audits = data.message;
        }
      );
    },
    calcIncomplete(provider) {
      // incomplete returns 1 if there are no joined rows, check then adjust
      if (provider.count_of_linked_rows === 0) {
        return 0;
      }
      return provider.incomplete;
    },
    getStatus(provider) {
      if (provider.complete > 0 && this.calcIncomplete(provider) === 0) {
        return "Completed";
      }
      return "In Progress";
    },
    auditPhysician(npi, provider_audit_id) {
      // user selected a record to work on
      Router.push({
        name: "audit-provider",
        params: { npi: npi, provider_audit_id: provider_audit_id }
      });
    },
    auditPatient(id) {
      // user selected a record to work on
      Router.push({ name: "audit-record", params: { audit_id: id } });
    }
  }
};
</script>

<style>
.v-datatable th,
.v-datatable td {
  padding-left: 5px !important;
  padding-right: 5px !important;
}
</style>